<template>
  <div id="container" ref="pdfContent" :class="isContrast?'contrast-container-pdf':'container-pdf'">
    <!--<button  v-if="!isContrast" class="close-but" @click="close">关闭</button>-->
    <div id="the-canvas-box"  v-if="displayId === 'the-canvas'">
      <i v-if="!isContrast" id="close-button" class="el-icon-error close-but" @click="close"></i>
      <canvas id="the-canvas"></canvas>
      <div v-if='pdfDoc' id="the-canvas-btns">
        <span class='' v-if="pageNum>1" @click="onPrevPage"><i class="el-icon-back prev-next"></i></span>
        <span class='' v-else :disabled="false"><i class="el-icon-back prev-next" style="color:white"></i></span>
        <span style="font-size: 14px;font-weight: bold;">第{{pageNum}}/{{pdfDoc.numPages}}页</span>
        <span class='' v-if="pageNum<pdfDoc.numPages" @click="onNextPage"><i class="el-icon-right prev-next"></i></span>
        <span class='' v-else :disabled="false"><i class="el-icon-right prev-next"  style="color:white"></i></span>
      </div>
      <i id="the-canvas-zoom-in" @click="changeCanvas(1)" class="el-icon-zoom-in zoom"></i>
      <i id="the-canvas-zoom-out" @click="changeCanvas(-1)" class="el-icon-zoom-out zoom"></i>
    </div>
    <div id="the-canvas-contrast-box"  v-if="displayId === 'the-canvas-contrast'">
      <i v-if="isContrast" id="close-button" class="el-icon-error close-but" @click="close"></i>
      <canvas id="the-canvas-contrast"></canvas>
      <div v-if='pdfDoc' id="the-canvas-contrast-btns">
        <span class='' v-if="pageNum>1" @click="onPrevPage"><i class="el-icon-back prev-next"></i></span>
        <span class='' v-else :disabled="false"><i class="el-icon-back prev-next" style="color:white"></i></span>
        <span style="font-size: 14px;font-weight: bold;">第{{pageNum}}/{{pdfDoc.numPages}}页</span>
        <span class='' v-if="pageNum<pdfDoc.numPages" @click="onNextPage"><i class="el-icon-right prev-next"></i></span>
        <span class='' v-else :disabled="false"><i class="el-icon-right prev-next"  style="color:white"></i></span>
      </div>
      <i id="the-canvas-contrast-zoom-in" @click="changeCanvas(1)" class="el-icon-zoom-in zoom"></i>
      <i id="the-canvas-contrast-zoom-out" @click="changeCanvas(-1)" class="el-icon-zoom-out zoom"></i>
    </div>
  </div>
</template>
<script>
import PDFJS from 'pdfjs-dist'
export default {
  name: 'pdf',
  props: {
    pdfUrl: String,
    isContrast: {
      default: false
    },
    displayId: String
  },
  data () {
    return {
      pdfDoc: null,
      pageNum: 1,
      pageRendering: false,
      pageNumPending: null,
      scale: 1.0,
      contrastFlg: this.isContrast
    }
  },
  mounted () {
    if (window.innerHeight < 950) {
      this.scale = 0.7
    }
    this.showPDF(this.pdfUrl)
  },
  methods: {
    showPDF (url) {
      let _this = this
      PDFJS.getDocument(url).then(function (pdf) {
        _this.pdfDoc = pdf
        _this.renderPage(1)
      })
      // document.body.style.overflow = 'hidden'
    },
    renderPage (num) {
      this.pageRendering = true
      let _this = this
      this.pdfDoc.getPage(num).then(function (page) {
        var viewport = page.getViewport(_this.scale)
        let Id = _this.displayId
        let canvas = document.getElementById(Id)
        canvas.height = viewport.height
        canvas.width = viewport.width
        // pdf衬底样式
        document.getElementById(Id + '-box').style.width = window.innerWidth * 0.4 + 'px'
        document.getElementById(Id + '-box').style.height = window.innerHeight * 0.9 + 'px'
        document.getElementById(Id + '-box').style.marginTop = window.innerHeight * 0.05 + 'px'
        document.getElementById(Id + '-box').style.overflow = 'auto'
        // 页码按钮div样式
        document.getElementById(Id + '-btns').style.width = window.innerWidth * 0.4 + 'px'
        // 如果pdf宽度超过衬底div，为滚动条留出宽度
        if (canvas.width > window.innerWidth * 0.4) {
          document.getElementById(Id + '-btns').style.bottom = window.innerHeight * 0.05 + 7 + 'px'
        } else {
          document.getElementById(Id + '-btns').style.bottom = window.innerHeight * 0.05 + 'px'
          if (Id === 'the-canvas') {
            document.getElementById(Id + '-btns').style.borderBottomLeftRadius = '10px'
          } else {
            document.getElementById(Id + '-btns').style.borderBottomRightRadius = '10px'
          }
        }
        // 如果pdf高度超过衬底div，为滚动条留出宽度
        if (canvas.height > window.innerHeight * 0.9) {
          document.getElementById(Id + '-btns').style.width = window.innerWidth * 0.4 - 7 + 'px'
        } else {
          document.getElementById(Id + '-btns').style.width = window.innerWidth * 0.4 + 'px'
        }
        // 关闭按钮样式和位置
        document.getElementById('close-button').style.top = window.innerHeight * 0.05 - 15 + 'px'
        // 放大缩小操作按钮样式
        document.getElementById('the-canvas-zoom-in').style.top = window.innerHeight * 0.45 - 30 + 'px'
        document.getElementById('the-canvas-zoom-out').style.top = window.innerHeight * 0.45 + 30 + 'px'
        if (_this.isContrast) {
          // 对比pdf时
          if (Id === 'the-canvas') {
            document.getElementById('the-canvas-zoom-in').style.left = window.innerWidth * 0.5 - 50 + 'px'
            document.getElementById('the-canvas-zoom-out').style.left = window.innerWidth * 0.5 - 50 + 'px'
          } else {
            document.getElementById('the-canvas-contrast-zoom-in').style.right = window.innerWidth * 0.1 + 20 + 'px'
            document.getElementById('the-canvas-contrast-zoom-out').style.right = window.innerWidth * 0.1 + 20 + 'px'
            document.getElementById('the-canvas-contrast-zoom-in').style.top = window.innerHeight * 0.45 - 30 + 'px'
            document.getElementById('the-canvas-contrast-zoom-out').style.top = window.innerHeight * 0.45 + 30 + 'px'
          }
          document.getElementById('close-button').style.left = window.innerWidth * 0.9 - 10 + 'px'
        } else {
          // 预览单个pdf
          document.getElementById('the-canvas-zoom-in').style.left = window.innerWidth * 0.7 - 50 + 'px'
          document.getElementById('the-canvas-zoom-out').style.left = window.innerWidth * 0.7 - 50 + 'px'
          document.getElementById('close-button').style.left = window.innerWidth * 0.7 - 10 + 'px'
          document.getElementById(Id + '-box').style.marginRight = window.innerWidth * 0.3 + 'px'
          document.getElementById(Id + '-box').style.borderRadius = '10px'
          if (canvas.height < window.innerHeight * 0.9) {
            document.getElementById(Id + '-btns').style.borderBottomRightRadius = '10px'
          }
        }
        document.body.style.overflow = 'hidden'
        // Render PDF page into canvas context
        var renderContext = {
          canvasContext: canvas.getContext('2d'),
          viewport: viewport
        }
        var renderTask = page.render(renderContext)

        // Wait for rendering to finish
        renderTask.promise.then(function () {
          _this.pageRendering = false
          if (_this.pageNumPending !== null) {
            // New page rendering is pending
            this.renderPage(_this.pageNumPending)
            _this.pageNumPending = null
          }
        })
      })
    },
    // 放大&缩小
    queueRenderPage (num) {
      if (this.pageRendering) {
        this.pageNumPending = num
      } else {
        this.renderPage(num)
      }
    },
    // 上一页
    onPrevPage () {
      if (this.pageNum <= 1) {
        return
      }
      this.pageNum--
      this.queueRenderPage(this.pageNum)
    },
    // 下一页
    onNextPage () {
      if (this.pageNum >= this.pdfDoc.numPages) {
        return
      }
      this.pageNum++
      this.queueRenderPage(this.pageNum)
    },
    hideContent () {
      this.$refs.pdfContent.style.display = 'block'
    },
    close () {
      if (this.isContrast) {
        let div_ls = document.getElementsByTagName('div')
        for (var i = 0; i < div_ls.length; i++) {
          // 遍历所有的div并根据id做判断
          if (div_ls[i].getAttribute('id') === 'container') {
            // 对满足条件的标签设置属性即可
            div_ls[i].style.display = 'none'
          }
        }
      } else {
        this.$refs.pdfContent.style.display = 'none'
      }
      document.body.style.overflow = 'auto'
    },
    changeCanvas (val) {
      let me = this
      if (val > 0) {
        me.scale += 0.1
      } else if (val < 0) {
        me.scale -= 0.1
      }
      me.queueRenderPage(me.pageNum)
    }
  }
}
</script>

<style scoped lang="scss">
  #the-canvas-contrast-box {
    text-align: center;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    margin-left: 3px;
    float:left;
    background-color: white;
    position: relative;
  }
  #the-canvas-box {
    text-align: center;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    margin-right: 3px;
    float:right;
    background-color: white;
    position: relative;
  }
  #the-canvas-btns{
    position: fixed;
    background-color: #d4d4d4;
    height: 50px;
    line-height: 50px;
    text-align: center;
  }
  #the-canvas-contrast-btns{
    position: fixed;
    background-color: #d4d4d4;
    height: 50px;
    line-height: 50px;
    text-align: center;
  }
  .container-pdf {
    background-color: rgba(0,0,0,0.75);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    text-align: center;
    border-radius: 10px;
    position: fixed;
    z-index: 999;
  }
  .contrast-container-pdf {
    background-color: rgba(0,0,0,0.75);
    width: 50%;
    height: 100%;
    top: 0;
    left: 0;
    text-align: center;
    position: fixed;
    z-index: 999;
  }
  .pdf-icon{
    color: white;
    font-size: 40px;
    cursor: pointer;
  }
  .close-but{
    position: fixed;
    color: red;
    border-radius: 50%;
    padding: 0px;
    font-size: 30px;
    cursor:pointer;
  }
  div::-webkit-scrollbar{
    width:6px;
    height:6px;
    background: #666666;
    /**/
  }
  div::-webkit-scrollbar-track{
    background: #ffffff;
    border-radius:2px;
  }
  div::-webkit-scrollbar-thumb{
    background: #666666;
    border-radius:10px;
    width: 2px;
  }
  div::-webkit-scrollbar-corner{
    background: #ffffff;
  }
  .prev-next {
    margin-left:10px;
    margin-right:10px;
    font-weight: bold;
    color: black;
    font-size: 16px;
  }
  .prev-next:hover {
    color: #6612cf;
    cursor: pointer;
  }
  .zoom {
    position: fixed;
    font-size: 30px;
    font-weight: bold;
    cursor: pointer;
    color:#d4d4d4
  }
  .zoom:hover{
    color:#6612cf;
  }
</style>
